<template>
  <div class="container py-5">
    <AdminNav />

    <form class="form-inline">
      <input
        type="text"
        class="form-control"
        name="categoryName"
        v-model="newCategoryName"
        placeholder="Create new category"
      />
      <button class="btn btn-secondary ml-2" @click.stop.prevent="createCategory">Create</button>
    </form>

    <br />
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">#</th>
          <th scope="col">Category</th>
          <th scope="col">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="category in categories" :key="category.id">
          <th scope="row">{{category.id}}</th>
          <td class="position-relative w-60">
            <div v-show="!category.isEditing" class="category-name">{{category.name}}</div>
            <input
              v-show="category.isEditing"
              v-model="category.name"
              type="text"
              class="form-control"
            />
            <span v-show="category.isEditing" class="cancel" @click="handleCancel(category.id)">✕</span>
          </td>
          <td>
            <button
              v-show="!category.isEditing"
              type="button"
              class="btn btn-link"
              @click.stop.prevent="toggleIsEditing(category.id)"
            >Edit</button>
            <button
              v-show="category.isEditing"
              type="button"
              class="btn btn-link"
              @click.stop.prevent="updateCategory({ categoryId: category.id, name: category.name })"
            >Save</button>
            <button
              type="button"
              class="btn btn-link"
              @click.stop.prevent="deleteCategory(category.id)"
            >Delete</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import AdminNav from "../components/AdminNav";
import adminAPI from "../apis/admin";
import { Toast } from "../utils/helpers";
// import { v4 as uuid } from "uuid";
export default {
  components: {
    AdminNav
  },
  data() {
    return {
      categories: [],
      newCategoryName: ""
    };
  },
  methods: {
    async fetchCategories() {
      try {
        const { data } = await adminAPI.categories.get();
        if (data.status === "error") {
          throw new Error(data.message);
        }
        this.categories = data.categories.map(category => ({
          ...category,
          isEditing: false,
          nameCached: ""
        }));
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error.message || "無法顯示類別，請稍後再試"
        });
      }
    },
    async createCategory() {
      try {
        // TODO: 透過 API 告知伺服器欲新增的餐廳類別,並且接收回傳的categoryId
        const { data } = await adminAPI.categories.create({
          name: this.newCategoryName
        });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.categories.push({
          id: data.categoryId,
          name: this.newCategoryName,
          isEditing: false,
          nameCached: ""
        });
        this.newCategoryName = "";
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error
        });
      }
    },
    async deleteCategory(categoryId) {
      try {
        // TODO: 透過 API 告知伺服器欲刪除的餐廳類別
        const { data } = await adminAPI.categories.delete({ categoryId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.categories = this.categories.filter(
          category => category.id !== categoryId
        );
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error
        });
      }
    },
    toggleIsEditing(categoryId) {
      this.categories.map(category => {
        if (category.id === categoryId) {
          category.isEditing = !category.isEditing;
          category.nameCached = category.name;
        }
      });
    },
    async updateCategory({ categoryId, name }) {
      try {
        // TODO: 透過 API 去向伺服器更新餐廳類別名稱
        const { data } = await adminAPI.categories.update({ categoryId, name });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.toggleIsEditing(categoryId);
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error
        });
      }
    },
    handleCancel(categoryId) {
      this.categories = this.categories.map(category => {
        if (category.id === categoryId) {
          return {
            ...category,

            // 把原本的餐廳類別名稱還回去
            name: category.nameCached
          };
        }

        return category;
      });
      this.toggleIsEditing(categoryId);
    }
  },
  created() {
    this.fetchCategories();
  }
};
</script>

<style scoped>
.category-name {
  padding: 0.375rem 0.75rem;
  border: 1px solid transparent;
  outline: 0;
  cursor: auto;
}

.btn-link {
  width: 62px;
}

.cancel {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border: 1px solid #aaaaaa;
  border-radius: 50%;
  user-select: none;
  cursor: pointer;
  font-size: 12px;
}

.w-60 {
  width: 60%;
}
</style>